import { Component, ElementRef, ViewChild } from '@angular/core';

@Component({
  selector: 'diary-mobile',
  templateUrl: './mobile.component.html',
  styleUrls: ['./mobile.component.scss']
})
export class DiaryMobileComponent {
  target: any = document.getElementsByClassName('abc');
  target2 = document.getElementsByClassName('xyz');
  flat = 1;
  check() {
    this.flat++
    if(this.flat % 2 == 0) {
      this.target[0].style = "green"
      this.target[0].innerHTML = "Số chẳn"
    } else {
      this.target[0].style = "yellow"
      this.target[0].innerHTML = "Số lẻ"      
    }
    
  }
}
